"use client";

import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import { v4 as uuidv4 } from "uuid";

type EducationModel = {
  id: string;
  name: string;
  philosophy: string;
  evaluationFocus: string;
  languageFocus: string;
  childFocus: string;
  updatedAt: string;
};

export default function CreateModelPage() {
  const router = useRouter();

  const [models, setModels] = useState<EducationModel[]>([]);
  const [form, setForm] = useState({
    name: "",
    philosophy: "",
    evaluationFocus: "",
    languageFocus: "",
    childFocus: "",
  });
  const [error, setError] = useState("");

  // ローカルストレージから既存モデルを読み込み
  useEffect(() => {
    const stored = localStorage.getItem("styleModels");
    if (stored) setModels(JSON.parse(stored));
  }, []);

  const handleChange = (field: keyof typeof form, value: string) => {
    setForm((prev) => ({ ...prev, [field]: value }));
  };

  const handleSave = () => {
    setError("");
    if (
      !form.name.trim() ||
      !form.philosophy.trim() ||
      !form.evaluationFocus.trim() ||
      !form.languageFocus.trim() ||
      !form.childFocus.trim()
    ) {
      setError("すべての必須項目を入力してください。");
      return;
    }

    const now = new Date().toISOString();

    const newModel: EducationModel = {
      id: uuidv4(),
      name: form.name.trim(),
      philosophy: form.philosophy.trim(),
      evaluationFocus: form.evaluationFocus.trim(),
      languageFocus: form.languageFocus.trim(),
      childFocus: form.childFocus.trim(),
      updatedAt: now,
    };

    const updatedModels = [newModel, ...models];
    localStorage.setItem("styleModels", JSON.stringify(updatedModels));
    setModels(updatedModels);

    // 保存後にトップページか一覧ページに遷移
    router.push("/models");
  };

  return (
    <main style={{ padding: 24, maxWidth: 600, margin: "0 auto", fontFamily: "sans-serif" }}>
      <h1 style={{ fontSize: 24, marginBottom: 16 }}>✏️ 新しい教育観モデルを作成</h1>

      {error && (
        <p style={{ color: "red", marginBottom: 12, fontWeight: "bold" }}>
          {error}
        </p>
      )}

      <label style={{ display: "block", marginBottom: 12 }}>
        モデル名（必須）：
        <input
          type="text"
          value={form.name}
          onChange={(e) => handleChange("name", e.target.value)}
          style={{ width: "100%", padding: 8, fontSize: 16, borderRadius: 6, border: "1px solid #ccc", marginTop: 4 }}
        />
      </label>

      <label style={{ display: "block", marginBottom: 12 }}>
        教育観（必須）：
        <textarea
          rows={3}
          value={form.philosophy}
          onChange={(e) => handleChange("philosophy", e.target.value)}
          style={{ width: "100%", padding: 8, fontSize: 16, borderRadius: 6, border: "1px solid #ccc", marginTop: 4 }}
        />
      </label>

      <label style={{ display: "block", marginBottom: 12 }}>
        評価観点の重視点（必須）：
        <textarea
          rows={3}
          value={form.evaluationFocus}
          onChange={(e) => handleChange("evaluationFocus", e.target.value)}
          style={{ width: "100%", padding: 8, fontSize: 16, borderRadius: 6, border: "1px solid #ccc", marginTop: 4 }}
        />
      </label>

      <label style={{ display: "block", marginBottom: 12 }}>
        言語活動の重視点（必須）：
        <textarea
          rows={3}
          value={form.languageFocus}
          onChange={(e) => handleChange("languageFocus", e.target.value)}
          style={{ width: "100%", padding: 8, fontSize: 16, borderRadius: 6, border: "1px solid #ccc", marginTop: 4 }}
        />
      </label>

      <label style={{ display: "block", marginBottom: 24 }}>
        育てたい子どもの姿（必須）：
        <textarea
          rows={3}
          value={form.childFocus}
          onChange={(e) => handleChange("childFocus", e.target.value)}
          style={{ width: "100%", padding: 8, fontSize: 16, borderRadius: 6, border: "1px solid #ccc", marginTop: 4 }}
        />
      </label>

      <button
        onClick={handleSave}
        style={{
          backgroundColor: "#4CAF50",
          color: "white",
          padding: "12px 24px",
          border: "none",
          borderRadius: 8,
          fontSize: 18,
          cursor: "pointer",
          width: "100%",
        }}
      >
        作成して保存
      </button>
    </main>
  );
}
